<template>
  <div class="page-wrap">
    <tj-header title="Checker "></tj-header>
    <section class="mt_15 padding_15">
      <div class="f16 f_bold mb_15"></div>
      <tj-checker v-model="checker" :gutter="15">
        <tj-checker-item :label="1"></tj-checker-item>
        <tj-checker-item :label="2"></tj-checker-item>
        <tj-checker-item :label="3"></tj-checker-item>
      </tj-checker>
    </section>

    <section class="mt_15 padding_15">
      <div class="f16 f_bold mb_15"></div>
      <tj-checker v-model="checker2" type="checkbox" :gutter="15">
        <tj-checker-item :label="1"></tj-checker-item>
        <tj-checker-item :label="2"></tj-checker-item>
        <tj-checker-item :label="3"></tj-checker-item>
      </tj-checker>
    </section>

    <section class="mt_15 padding_15">
      <div class="f16 f_bold mb_15"></div>
      <p class="mb_15 bd_b_1"><span class="gray">:</span> {{checker3.join(' ')}}</p>
      <tj-checker v-model="checker3" type="checkbox" size="small" :gutter="15">
        <tj-checker-item label=""></tj-checker-item>
        <tj-checker-item label=""></tj-checker-item>
        <tj-checker-item label=""></tj-checker-item>
      </tj-checker>

      <p class="mt_15 mb_15 bd_b_1"><span class="gray">:</span> {{checker4}}</p>
      <tj-checker class="mt_15" v-model="checker4" size="large" round="25px" :gutter="15">
        <tj-checker-item label=""></tj-checker-item>
        <tj-checker-item label=""></tj-checker-item>
      </tj-checker>
    </section>

  </div>
</template>

<script>
export default {
    data() {
      return {
        checker: 1,
        checker2: 1,
        checker3: [],
        checker4: "",
      }
    }
}
</script>

<style>

</style>



