<template>
  <div class="page-wrap">
    <tj-header title="Toast "></tj-header>
    <section class="mt_15 padding_15">
      <div class="f16 f_bold mb_15"></div>
      <tj-button size="small" class="mr_10 mb_15" round="3px" @click="handleTip"></tj-button>
      <tj-button size="small" type="success" class="mr_10 mb_15" @click="handleLongTip" round="3px"></tj-button>
      <tj-button size="small" type="danger" class="mr_10 mb_15" @click="handleOptions" round="3px"></tj-button>
    </section>
  </div>
</template>

<script>
export default {
  methods: {
    handleTip() {
      this.$toast("");
    },
    handleLongTip() {
      this.$toast("");
    },
    handleOptions() {
      this.$toast({
        message: '',
        iconClass: 'tj-icon tj-success_no_circle',
        position: 'bottom'
      })
    },
  }
};
</script>



