import Vue from 'vue'
import Router from 'vue-router'

import index from '@/pages/index'
import layout from '@/pages/layout'
import button from '@/pages/button'
import cell from '@/pages/cell'
import popup from '@/pages/popup'

import checkbox from '@/pages/checkbox'
import field from '@/pages/field'
import swicth from '@/pages/swicth'
import rate from '@/pages/rate'
import checker from '@/pages/checker'
import upload from '@/pages/upload'
import range from '@/pages/range'
import select from '@/pages/select'

import swipe from '@/pages/swipe'
import loading from '@/pages/loading'
import collapse from '@/pages/collapse'
import progress from '@/pages/progress'
import price from '@/pages/price'
import panel from '@/pages/panel'
import recycleList from '@/pages/recycleList'

import toast from '@/pages/toast'
import dialog from '@/pages/dialog'
import indicator from '@/pages/indicator'


Vue.use(Router)

export default new Router({
  routes: [
    // 
    {
      path: '/',
      name: '',
      component: index
    },
    // 
    {
      path: '/layout',
      label: '',
      name: 'Layout ',
      component: layout
    },
    {
      path: '/button',
      label: '',
      name: 'Button ',
      component: button
    },
    {
      path: '/cell',
      label: '',
      name: 'Cell ',
      component: cell
    },
    {
      path: '/popup',
      label: '',
      name: 'Popup ',
      component: popup
    },
    // 
    {
      path: '/checkbox',
      label: '',
      name: 'Checkbox ',
      component: checkbox
    },
    {
      path: '/field',
      label: '',
      name: 'Field ',
      component: field
    },
    {
      path: '/switch',
      label: '',
      name: 'Switch ',
      component: swicth
    },
    {
      path: '/rate',
      label: '',
      name: 'Rate ',
      component: rate
    },
    {
      path: '/checker',
      label: '',
      name: 'Checker ',
      component: checker
    },
    {
      path: '/upload',
      label: '',
      name: 'Upload ',
      component: upload
    },
    {
      path: '/range',
      label: '',
      name: 'Range ',
      component: range
    },
    {
      path: '/select',
      label: '',
      name: 'Select ',
      component: select
    },
    // 
    {
      path: '/swipe',
      label: '',
      name: 'Swipe ',
      component: swipe
    },
    {
      path: '/loading',
      label: '',
      name: 'Loading ',
      component: loading
    },
    {
      path: '/collapse',
      label: '',
      name: 'Collapse ',
      component: collapse
    },
    {
      path: '/progress',
      label: '',
      name: 'Progress ',
      component: progress
    },
    {
      path: '/price',
      label: '',
      name: 'Price ',
      component: price
    },
    {
      path: '/panel',
      label: '',
      name: 'Panel ',
      component: panel
    },
    {
      path: '/recycleList',
      label: '',
      name: 'RecycleList ',
      component: recycleList
    },
    // 
    {
      path: '/toast',
      label: '',
      name: 'Toast ',
      component: toast
    },
    {
      path: '/dialog',
      label: '',
      name: 'Dialog ',
      component: dialog
    },
    {
      path: '/indicator',
      label: '',
      name: 'Indicator ',
      component: indicator
    },
    // 
    {
      path: '/tabs',
      label: '',
      name: 'Tabs ',
      component: index
    },
    {
      path: '/header',
      label: '',
      name: 'Header ',
      component: index
    },
    {
      path: '/search',
      label: '',
      name: 'Search ',
      component: index
    },
  ]
})


