<template>
  <div class="page-wrap">
    <tj-header title="Dialog "></tj-header>
    <section class="mt_15 padding_15">
      <div class="f16 f_bold mb_15"></div>
      <tj-button size="small" class="mr_10 mb_15" round="3px" @click="handle"></tj-button>
      <tj-button
        size="small"
        type="success"
        class="mr_10 mb_15"
        @click="handle2"
        round="3px"
      ></tj-button>
      <tj-button
        size="small"
        type="danger"
        class="mr_10 mb_15"
        @click="handle3"
        round="3px"
      ></tj-button>
    </section>
  </div>
</template>

<script>
export default {
  methods: {
    handle() {
      this.$dialog.alert("!", "");
    },
    handle2() {
      this.$dialog
        .confirm("?", "")
        .then(() => {
          console.log("");
        })
        .catch(() => {
          console.log("");
        });
    },
    handle3() {
      this.$dialog.prompt(" ", "").then(({ value }) => {
        if (value) {
          this.$dialog.alert(` ${value}`, "");
        }
      });
    }
  }
};
</script>



