<template>
  <div class="page-wrap">
    <tj-header title="Select "></tj-header>
    <tj-cell class="mt_15" title="" :value="value" @click="visible = true"></tj-cell>
    <tj-cell class="mt_15" title="" :value="value2" @click="visible2 = true"></tj-cell>
    <tj-cell class="mt_15" title="">
      <span class="gray" @click="visible3 = true">{{value3.join(",")}}</span>
    </tj-cell>
    <tj-select title="" v-model="value" :visible.sync="visible" :options="options"></tj-select>
    <tj-select
      title=""
      v-model="value2"
      :visible.sync="visible2"
      value-key="name"
      select-key="name"
      :options="options2"
    ></tj-select>
    <tj-select
      title=""
      v-model="value3"
      :visible.sync="visible3"
      value-key="name"
      select-key="name"
      :item-count="3"
      :options="options3"
    ></tj-select>
  </div>
</template>

<script>
export default {
  data() {
    return {
      visible: false,
      value: "",
      options: ["", "", "", "", ""],
      visible2: false,
      value2: "1",
      options2: [],
      visible3: false,
      value3: ["0", "0", "0"],
      options3: []
    };
  },
  mounted() {
    for (var i = 0; i < 5; i++) {
      this.options2.push({
        id: i,
        name: "" + i
      });
      // this.value3.push({
      //   id: i,
      //   name: "" + i
      // });

      var children = [];

      for (var j = 0; j < 5; j++) {
        var childrens = [];
        for (var k = 0; k < 5; k++) {
          childrens.push({
            id: k,
            name: "" + k
          });
        }

        children.push({
          id: j,
          name: "" + j,
          children: childrens
        });
      }

      this.options3.push({
        id: i,
        name: "" + i,
        children: children
      });
    }
  }
};
</script>

<style>
</style>



