<template>
  <div class="page-wrap">
    <tj-header title="Checkbox "></tj-header>

    <section class="mt_15">
      <tj-field
        class="bd_b_1 mt_15"
        icon="red tj-ren"
        title=""
        v-model="name"
        placeholder=""
      ></tj-field>
      <tj-field
        class="bd_b_1 mt_15"
        icon="red tj-dianhua1"
        title=""
        type="number"
        v-model="pnone"
        placeholder=""
      ></tj-field>
      <tj-field
        class="bd_b_1 mt_15"
        icon="red tj-gangbi"
        title=""
        type="password"
        v-model="password"
        placeholder=""
      ></tj-field>
      <tj-field
        class="bd_b_1 mt_15"
        title=""
        type="textarea"
        v-model="desc"
        rows="3"
        placeholder=""
      ></tj-field>
      <tj-field
        class="bd_b_1 mt_15"
        label-width="100px"
        must
        title=""
        v-model="code"
        placeholder=""
      >
        <tj-button class="mr_10" plain round size="mini"></tj-button>
      </tj-field>
      <tj-field class="mt_10 bd_b_1" v-model="desc" placeholder=""></tj-field>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      name: "",
      pnone: "",
      password: "",
      code: "",
      desc: ""
    };
  }
};
</script>



